/**
  * Funky Size Mixin
  * @include size(<width>, <height>);
  * @include size(100px, 50px);
 */
.footer {
  padding: 20px 0;
  border-top: 1px solid #eaeaea;
  font-size: 14px;
  font-size: 0.77778rem;
  line-height: 18px;
  line-height: 1rem; }
  .footer img {
    width: 200px; }
  .footer__navigation ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px; }
  .footer__navigation li {
    padding: 0 15px 0 4px; }
  @media only screen and (max-width: 1159px) {
    .footer .footer__nav-wrapper {
      width: 100%;
      display: block;
      max-width: none;
      flex-basis: 100%; }
      .footer .footer__nav-wrapper li {
        margin: 20px 15px 0 0px; }
        .footer .footer__nav-wrapper li:last-child {
          margin: 20px 15px 0 0px; } }
  .footer nav ul {
    list-style: none;
    margin: 0;
    padding: 0; }
    .footer nav ul li {
      margin: 0 15px; }
      .footer nav ul li:last-child {
        margin: 0 0 0 15px; }
      .footer nav ul li a {
        display: inline-block;
        padding-right: 10px;
        position: relative;
        font-size: 14px;
        font-size: 0.77778rem;
        line-height: 18px;
        line-height: 1rem; }
        .footer nav ul li a:after {
          font-family: 'icon-font';
          content: "\f105";
          display: inline-block;
          line-height: 1;
          color: inherit;
          position: absolute;
          right: 0;
          top: 2px;
          display: inline-block;
          height: 14px;
          overflow: hidden; }
        .footer nav ul li a:hover:after {
          text-decoration: none; }

.footer__lang {
  position: fixed;
  bottom: 69px;
  right: 0;
  text-align: center;
  z-index: 999; }
  .footer__lang .footer__lang-inner {
    margin: 0 10px;
    position: relative; }
    .footer__lang .footer__lang-inner > div {
      display: inline-block;
      background-color: #fff;
      position: relative;
      float: right;
      padding: 5px 10px;
      border-radius: 10px; }
