/*
 * Name: font size mixin
 * Author: Tjark Süßen
 * Description:
 */
/*
 Usage:
 @include font-size(font-size, line-height);
 */
/**
*   @include icon($icon-facebook);
*   @include icon($icon-facebook, 'after') { color:red; }
*/
/**
  * Funky Size Mixin
  * @include size(<width>, <height>);
  * @include size(100px, 50px);
 */
.input {
  margin-bottom: 25px; }
  .input .input__label {
    display: block;
    margin-bottom: 10px;
    font-size: 16px;
    font-size: 0.88889rem;
    line-height: 18px;
    line-height: 1rem; }
  .input .input__field {
    color: #323232;
    border: 1px solid #d4d4d4;
    padding: 14px 16px;
    outline: none;
    width: 100%; }
    .input .input__field.input--error {
      border: 1px solid #d81018; }
  .input textarea, .input .input__field--rte {
    resize: vertical; }
    .input textarea.input--error, .input .input__field--rte.input--error {
      border: 1px solid #d81018;
      padding: 12px 16px; }
  .input .input__field--rte {
    overflow-x: hidden;
    overflow-y: auto;
    height: 220px;
    min-height: 220px; }
    .input .input__field--rte.input--error {
      border: 1px solid #d81018; }
  .input.input--error .input__label {
    color: #d81018; }
  .input.input--error .input__error {
    font-size: 14px;
    font-size: 0.77778rem;
    line-height: 18px;
    line-height: 1rem;
    color: #d81018;
    padding-top: 5px; }
  .input input[disabled] {
    background-color: #fbfbfb;
    cursor: no-drop; }
