*/
/**
  * Funky Size Mixin
  * @include size(<width>, <height>);
  * @include size(100px, 50px);
 */
.my-account-menu {
  margin: 0;
  padding: 0; }
  .my-account-menu li {
    border-top: 1px solid #eaeaea;
    list-style: none;
    padding: 0;
    margin: 0;
    display: block; }
    .my-account-menu li a {
      display: block;
      padding: 15px 25px;
      font-weight: bold; }
      .my-account-menu li a:hover {
        text-decoration: none;
        background-color: #F9B000; }
    .my-account-menu li:last-child a {
      color: #F9B000;
      position: relative;
      display: block; }
      .my-account-menu li:last-child a:before {
        font-family: 'icon-font';
        content: "\f061";
        display: inline-block;
        line-height: 1;
        color: #F9B000;
        margin-right: 10px;
        text-decoration: none; }
      .my-account-menu li:last-child a:hover {
        background-color: transparent;
        text-decoration: underline; }
  .my-account-menu .my-account-menu--active {
    background-color: #F9B000; }
  .my-account-menu .my-account-menu__spacer {
    height: 54px; }
