/*
 * Name: font size mixin
 * Author: Tjark Süßen
 * Description:
 */
/*
 Usage:
 @include font-size(font-size, line-height);
 */
/**
*   @include icon($icon-facebook);
*   @include icon($icon-facebook, 'after') { color:red; }
*/
/**
  * Funky Size Mixin
  * @include size(<width>, <height>);
  * @include size(100px, 50px);
 */
.btn {
  position: relative;
  cursor: pointer;
  line-height: 1;
  border: none;
  outline: none;
  font-weight: bold;
  display: inline-block;
  text-decoration: none;
  text-align: center;
  border-radius: 0;
  font-family: Arial;
  background-color: #F9B000;
  padding: 15px 20px; }
  .btn[disabled] {
    opacity: 0.8;
    cursor: not-allowed; }
  .btn > i {
    position: absolute;
    left: 19px;
    display: flex;
    align-items: center;
    height: 100%;
    top: 0; }
  .btn.btn--icon {
    padding: 15px 20px 15px 44px; }
  .btn:hover, .btn:focus, .btn:active {
    text-decoration: none;
    background-color: #f78c00; }
  .btn--active {
    text-decoration: none;
    background-color: #f78c00; }
  .btn--full {
    width: 100%; }
  .btn--small {
    padding: 5px 10px; }
  .btn--loading {
    position: relative; }
    .btn--loading:after {
      position: absolute;
      text-align: center;
      content: " ";
      display: block;
      width: 20px;
      height: 20px;
      margin: 1px;
      top: 9px;
      left: calc(50% - 17px);
      border-radius: 50%;
      border: 5px solid #323232;
      border-color: #323232 transparent #323232 transparent;
      animation: lds-dual-ring 1.2s linear infinite; }
    .btn--loading span {
      visibility: hidden; }

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }
