/**
  * Funky Size Mixin
  * @include size(<width>, <height>);
  * @include size(100px, 50px);
 */
.border-box {
  border: 1px solid #eaeaea;
  padding: 25px 40px 25px 25px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  position: relative;
  align-items: normal; }
  .border-box .border-box__content {
    display: block;
    width: 100%; }
  .border-box .border-box__headline {
    margin-bottom: 5px; }
  .border-box .border-box__text {
    margin: 0; }
  .border-box:hover {
    background-color: #F9B000;
    text-decoration: none; }
  .border-box .border-box__text--new {
    color: #F9B000;
    font-weight: bold; }
  .border-box:hover .border-box__text--new {
    color: #000; }
  .border-box.border-box--arrow {
    justify-content: flex-start;
    align-items: flex-start; }
    .border-box.border-box--arrow .border-box__headline {
      margin-bottom: 20px; }
  .border-box .border-box__arrow {
    position: absolute;
    bottom: 20px;
    right: 20px;
    z-index: 5; }
    .border-box .border-box__arrow:after {
      font-family: 'icon-font';
      content: "\f105";
      display: inline-block;
      line-height: 1;
      color: #323232;
      font-size: 36px;
      font-size: 2rem;
      line-height: 18px;
      line-height: 1rem;
      font-weight: bold; }
  .border-box.border-box--container {
    width: 100%;
    padding: 25px; }
    .border-box.border-box--container .border-box__arrow {
      display: none; }
    .border-box.border-box--container:hover {
      background-color: transparent; }
    .border-box.border-box--container .border-box__headline {
      font-family: "Signika", "sans-serif";
      font-weight: bold; }
