.nav {
  height: 100%; }
  .nav .nav__items {
    height: 100%;
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    justify-content: flex-end; }
    .nav .nav__items .nav__item {
      margin-left: 42px;
      align-items: flex-end;
      justify-content: flex-end;
      display: flex;
      border-bottom: 4px solid transparent; }
      @media only screen and (max-width: 945px) {
        .nav .nav__items .nav__item {
          margin-left: 16px; } }
      .nav .nav__items .nav__item a {
        text-align: right;
        padding-bottom: 10px;
        text-transform: uppercase;
        font-weight: bold;
        text-decoration: none;
        display: block;
        color: #00519E;
        white-space: nowrap;
        font-family: "Signika", "sans-serif"; }
        .nav .nav__items .nav__item a:hover, .nav .nav__items .nav__item a:focus, .nav .nav__items .nav__item a:active {
          color: #00519E; }
        @media only screen and (max-width: 1099px) {
          .nav .nav__items .nav__item a {
            font-size: 14px;
            font-size: 0.77778rem;
            line-height: 18px;
            line-height: 1rem; } }
      .nav .nav__items .nav__item.nav__item--login a, .nav .nav__items .nav__item:last-child:not(:first-child) a {
        color: #323232;
        padding-left: 25px;
        position: relative;
        text-transform: none; }
        .nav .nav__items .nav__item.nav__item--login a:before, .nav .nav__items .nav__item:last-child:not(:first-child) a:before {
          font-family: 'icon-font';
          content: "\f007";
          display: inline-block;
          line-height: 1;
          color: #323232;
          position: absolute;
          left: 0;
          top: 1px; }
      .nav .nav__items .nav__item.nav__item--login:hover, .nav .nav__items .nav__item:last-child:not(:first-child):hover {
        border-bottom: 4px solid #323232; }
      .nav .nav__items .nav__item.nav__item--active, .nav .nav__items .nav__item:hover {
        border-bottom: 4px solid #00519E; }
    .nav .nav__items .nav__item--logout-mobile {
      display: none; }
      @media only screen and (max-width: 768px) {
        .nav .nav__items .nav__item--logout-mobile {
          display: block; } }
  .nav.nav--tab {
    padding: 45px 0 0;
    margin: 0 0 35px; }
    @media only screen and (max-width: 768px) {
      .nav.nav--tab {
        padding: 15px 0 0; } }
    .nav.nav--tab .nav__items {
      border-bottom: 1px solid #eaeaea;
      height: auto;
      justify-content: flex-start; }
      .nav.nav--tab .nav__items .nav__item {
        margin-left: 0;
        margin-right: 40px; }
        .nav.nav--tab .nav__items .nav__item:last-child a {
          padding-left: 0; }
          .nav.nav--tab .nav__items .nav__item:last-child a:before {
            display: none; }
        .nav.nav--tab .nav__items .nav__item a {
          text-transform: none;
          color: #323232;
