        .nav.nav--tab .nav__items .nav__item.nav__item--active, .nav.nav--tab .nav__items .nav__item:hover {
          border-bottom: 4px solid #F9B000; }
          .nav.nav--tab .nav__items .nav__item.nav__item--active a, .nav.nav--tab .nav__items .nav__item:hover a {
