main > div > .container:first-child > h1,
main > .container:first-child > h1 {
  margin-top: 40px; }
  @media only screen and (max-width: 768px) {
    main > div > .container:first-child > h1,
    main > .container:first-child > h1 {
      margin-top: 20px; } }

.container {
  max-width: 1242px;
  margin: 0 auto;
  position: relative;
  z-index: 5; }
  .container.container--border {
    padding: 50px 0px;
    border-top: 1px solid #eaeaea;
    border-bottom: 1px solid #eaeaea; }
  .container.container--more {
    position: relative;
    z-index: 4; }
  .container > h1, .container > h2, .container > h3, .container > h4 {
    text-align: center; }

.container__show-more {
  position: absolute;
  right: 0;
  top: 14px;
  padding-right: 15px;
  color: #F9B000; }
  .container__show-more a {
    color: #F9B000;
    font-weight: bold; }
  .container__show-more:after {
    font-family: 'icon-font';
    content: "\f105";
    display: inline-block;
    line-height: 1;
    color: inherit;
    position: absolute;
    right: 0;
    top: 3px; }
  @media only screen and (max-width: 945px) {
    .container__show-more {
      position: relative;
      text-align: right;
      top: 0;
      margin: 15px 0 0 0; } }

main > div > .container:first-child,
main > custom-add-to-homescreen > .container:first-child,
main > .container:first-child {
  z-index: 6; }

.container-fluid {
  margin-right: auto;
  margin-left: auto;
  padding-right: 12px;
  padding-left: 12px; }
