*/
/**
  * Funky Size Mixin
  * @include size(<width>, <height>);
  * @include size(100px, 50px);
 */
.my-account {
  position: relative; }
  .my-account h1 {
    font-weight: 600; }
  .my-account .divider-top {
    margin-top: 20px;
    padding-top: 20px;
    border-top: 1px solid #999; }
  .my-account .my-account__download {
    font-size: 13px;
    font-size: 0.72222rem;
    line-height: 18px;
    line-height: 1rem;
    color: #F9B000;
    font-weight: bold; }
    .my-account .my-account__download:before {
      content: "↓";
      padding-right: 5px; }
  .my-account .rte-margin {
    padding-top: 15px; }
  .my-account .my-account__individuell-info {
    font-style: italic;
    color: #7a7a7a; }
  .my-account .my-account__cancel-link {
    color: #F9B000;
    position: relative;
    padding-left: 25px;
    font-weight: bold; }
    .my-account .my-account__cancel-link:before {
      font-family: 'icon-font';
      content: "\f057";
      display: inline-block;
      line-height: 1;
      color: #F9B000;
      position: absolute;
      left: 0px;
      top: 2px;
      height: 18px;
      width: 18px;
      overflow: hidden; }
  .my-account .border-box .custom-map {
    margin: 0 -25px -25px -25px;
    width: calc(100% + 50px); }
  .my-account .my-account__add-link {
    color: #F9B000;
    font-weight: bold;
    display: inline-block;
    position: relative;
    padding-left: 15px; }
    .my-account .my-account__add-link:before {
      content: "+";
      position: absolute;
      left: 0;
      top: 1px; }
  .my-account .my-account__timerow {
    display: flex;
    flex-direction: row; }
    @media only screen and (max-width: 945px) {
      .my-account .my-account__timerow {
        flex-wrap: wrap; } }
    .my-account .my-account__timerow .input--time {
      width: 110px; }
    .my-account .my-account__timerow .input--select {
      width: 220px; }
    .my-account .my-account__timerow > div {
      flex: 1 1 auto;
      margin-right: 10px;
      margin-bottom: 0; }
      @media only screen and (max-width: 945px) {
        .my-account .my-account__timerow > div {
          width: 50%; }
          .my-account .my-account__timerow > div:nth-child(even) {
            margin-right: 0; } }
      .my-account .my-account__timerow > div:last-child {
        margin-right: 0; }
  .my-account .my-account__status {
    background-color: #008000;
    text-align: center;
    padding: 8px;
    color: #fff;
    position: absolute;
    width: 100%;
    top: -98px;
    left: 0;
    font-size: 14px;
    font-size: 0.77778rem;
    line-height: 18px;
    line-height: 1rem; }
    @media only screen and (max-width: 768px) {
      .my-account .my-account__status {
        top: 0;
        position: relative;
        margin-bottom: 30px; } }
  .my-account .my-account__status--wait {
    background-color: #f9b000;
    color: #000; }
  .my-account .my-account__seperator {
    margin: 0 8px; }
  .my-account .my-account__label {
    margin-right: 15px; }
  .my-account .my-account__dates {
    display: flex;
    flex-direction: row;
    align-items: center; }
    .my-account .my-account__dates > custom-datepicker {
      width: 20%; }
      @media only screen and (max-width: 768px) {
        .my-account .my-account__dates > custom-datepicker {
          width: 100%; } }
    @media only screen and (max-width: 768px) {
      .my-account .my-account__dates {
        flex-direction: column; } }
    .my-account .my-account__dates > :first-child {
      flex: 0 1 auto; }
      @media only screen and (max-width: 768px) {
        .my-account .my-account__dates > :first-child {
          margin-right: 0; } }
  .my-account dl {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0; }
    .my-account dl.hide {
      display: none; }
    .my-account dl dd {
      margin: 0 0 15px;
      flex: 1 1 auto;
      width: 50%;
      padding-bottom: 15px;
      border-bottom: 1px solid #eaeaea; }
      @media only screen and (max-width: 768px) {
        .my-account dl dd {
          width: 100%; } }
    .my-account dl dt {
      margin: 0 0 15px;
      flex: 1 1 auto;
      width: 50%;
      padding-bottom: 15px;
      border-bottom: 1px solid #eaeaea; }
      @media only screen and (max-width: 768px) {
        .my-account dl dt {
          width: 100%; } }
