 Usage:
 @include font-size(font-size, line-height);
 */
/**
*   @include icon($icon-facebook);
*   @include icon($icon-facebook, 'after') { color:red; }
*/
/**
  * Funky Size Mixin
  * @include size(<width>, <height>);
  * @include size(100px, 50px);
 */
.breadcrumb {
  padding: 4px 0 40px; }
  .breadcrumb a {
    color: #F9B000;
    font-weight: bold;
    position: relative;
    padding-right: 15px;
    margin-right: 15px;
    font-size: 14px;
    font-size: 0.77778rem;
    line-height: 18px;
    line-height: 1rem; }
    .breadcrumb a:after {
      font-family: 'icon-font';
      content: "\f054";
      display: inline-block;
      line-height: 1;
      color: #F9B000;
      position: absolute;
      right: -5px;
      top: 4px;
      font-size: 10px; }
    .breadcrumb a:last-child:after {
      display: none; }
