/**
  * Funky Size Mixin
  * @include size(<width>, <height>);
  * @include size(100px, 50px);
 */
header.header {
  position: fixed;
  max-height: 100px;
  width: 100%;
  top: 0;
  left: 0;
  background-color: #fff;
  z-index: 60; }
  header.header > .container {
    border-bottom: 1px solid #eaeaea; }
  header.header .header__logo {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    margin: 15px 0; }
    header.header .header__logo .header__logo__svg {
      border-right: 1px solid #000;
      padding-right: 15px;
      margin-right: 15px;
      position: relative;
      z-index: 9999;
      min-width: 126px;
      max-width: 216px;
      width: 100%; }
      @media only screen and (max-width: 1049px) {
        header.header .header__logo .header__logo__svg {
          max-width: 126px; } }
      header.header .header__logo .header__logo__svg a {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%; }
        header.header .header__logo .header__logo__svg a img {
          display: block; }
    header.header .header__logo .header__logo__title {
      text-transform: uppercase;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      align-items: flex-end;
      position: relative;
      z-index: 9999; }
      header.header .header__logo .header__logo__title div {
        font-family: "Signika", "sans-serif"; }
      @media only screen and (max-width: 1049px) {
        header.header .header__logo .header__logo__title {
          font-size: 11px;
          font-size: 0.61111rem;
          line-height: 18px;
          line-height: 1rem; } }
