*/
/**
  * Funky Size Mixin
  * @include size(<width>, <height>);
  * @include size(100px, 50px);
 */
.info-box {
  position: relative;
  padding: 20px 20px 20px 56px;
  background-color: #eaeaea;
  font-size: 15px;
  font-size: 0.83333rem;
  line-height: 22px;
  line-height: 1.22222rem; }
  .info-box:before {
    font-family: 'icon-font';
    content: "\f129";
    display: inline-block;
    line-height: 1;
    color: #323232;
    font-size: 20px;
    font-size: 1.11111rem;
    line-height: 18px;
    line-height: 1rem;
    position: absolute;
    left: 26px;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    flex-direction: row; }
